import { Box, Container, Typography } from '@mui/material'

import React from 'react'

export default function HeroHead() {
    return (
        <>
            <Box sx={{width:"100%", padding:"200px 0"}}>
                <Container sx={{ width: "90%" }}>



                    <Box sx={{
                        border:  {
                            xxs: "10px solid #F4F0EC",
                            xs: "10px solid #F4F0EC",
                            sm: "12px solid #F4F0EC",
                            md: "12px solid #F4F0EC",
                            lg: "12px solid #F4F0EC",
                            xl: "12px solid #F4F0EC"
                        },
                        
                        height:  {
                            xxs: "400px",
                            xs: "400px",
                            sm: "480px",
                            md: "480px",
                            lg: "480px",
                            xl: "480px"
                        },
                      
                        borderRadius: {
                            xxs: "50px",
                            xs: "70px",
                            sm: "70px",
                            md: "100px",
                            lg: "100px",
                            xl: "100px"
                        },
                        padding: {
                            xxs: "20px 5px",
                            xs: "30px 10px",
                            sm: "90px 10px",
                            md: "90px 10px",
                            lg: "90px 10px",
                            xl: "90px 10px"
                        }, 
                       

                        }}>


                        <Container sx={{
                            bgcolor: "white",
                            height: "520px",
                            width: {
                                xxs: "50px",
                                xs: "150px",
                                sm: "300px",
                                md: "500px",
                                lg: "500px",
                                xl: "500px"
                            },
                            position: "relative",
                            bottom: "130px",
                            margin:"0 auto"


                        }}>



                        </Container>
                        <Box sx={{ position: "relative", bottom:{
                                xxs: "540px",
                                xs: "540px",
                                sm: "500px",
                                md: "500px",
                                lg: "500px",
                                xl: "500px"
                            },
                            padding:" 0 20px"  }}>
                            <Typography variant='h3' sx={{ textAlign: "center", fontWeight:"bold" }}></Typography>    
                            <Typography variant='h6' sx={{ textAlign: "center", padding:"10px 0", fontWeight:"bold" }}>I like an interior that defies labeling. I don't really want someone to walk into a room and know that I did it</Typography>
                            <Typography sx={{textAlign:"center"}}>-BUNNY WILLIAMS</Typography>
                        </Box>
                    </Box>


                </Container>
            </Box>
        </>
    )
}
