import { ThemeProvider } from '@emotion/react'
import React from 'react'
import { appTheme } from '../theme/theme'
import ArrowForwardIcon from '@mui/icons-material/ArrowForward';
import { Button, Typography } from '@mui/material';

export default function ButtonWithoutBorder() {
    return (
        <ThemeProvider theme={appTheme}>
            <Button sx={{}}>
                <Typography sx={{textTransform:"capitalize", color:appTheme.palette.secondary.main, marginRight:"5px", fontWeight:"bold"}}>Read More
                 </Typography>
                 <ArrowForwardIcon sx={{color:appTheme.palette.primary.main}}/>
            </Button>
        </ThemeProvider>
    )
}
