import React from 'react'
import { Box, Card, CardActionArea, CardContent, Container, Typography } from '@mui/material'
import { ThemeProvider } from '@emotion/react'
import { appTheme } from '../theme/theme'
import ButtonWithoutBorder from './ButtonWithoutBorder'





const cards = [
    {
        id: 1,
        name: 'Project Plan',
        title: 'There are many variations of the passages of lorem Ipsum from    available, majority. ',
        button: <ButtonWithoutBorder />

    },
    {
        id: 2,
        name: 'Interior Work',
        title: 'There are many variations of the passages of lorem Ipsum from    available, majority. ',
        button: <ButtonWithoutBorder />
    },
    {
        id: 3,
        name: 'Realization',
        title: 'There are many variations of the passages of lorem Ipsum from    available, majority. ',
        button: <ButtonWithoutBorder />
    }

]

const boxStyle = {

    display: "flex",
    justifyContent: {
        xxs: "center",
        xs: "center",
        sm: "center",
        md: "space-between",
        lg: "space-between",
        xl: "space-between"
    },
    flexWrap: {
        xxs: "wrap",
        xs: "wrap",
        sm: "wrap",
        md: "nowrap",
        lg: "nowrap",
        xl: "nowrap"
    }

}


export default function Overview() {
    return (
        <ThemeProvider theme={appTheme}>
            <Box sx={{ width: "100%", padding: "100px 0"}}>
                <Container fixed sx={{ width: "90%", margin: "0 auto" }}>
                   

                    <Box sx={boxStyle}>
                        {cards.map((elem, i) => {
                            return (
                                <>
                                    <Card key={i} sx={{
                                        borderRadius: "0", marginBottom: "15px", border:"none",
                                        boxShadow:"none",
                                        marginRight: {
                                            xxs: "0",
                                            xs: "15px",
                                            sm: "15px",
                                            md: "15px",
                                            lg: "15px",
                                            xl: "15px"
                                        }
                                    }}>
                                        <CardActionArea>
                                           
                                            <CardContent sx={{  paddingTop: "50px" }}>
                                                <Typography variant='h6' sx={{  marginBottom: "10px", fontWeight:"bold", textAlign:"center" }}>{elem.name}</Typography>
                                                <Typography variant='body1' sx={{textAlign:"center"}}>{elem.title}</Typography>
                                                <Box sx={{display:"flex", justifyContent:"center"}}>{elem.button}</Box>
                                            </CardContent>
                                        </CardActionArea>
                                    </Card>
                                </>
                            )
                        })}
                    </Box>
                </Container>


            </Box>

        </ThemeProvider>
    )
}




