import React from "react";
import { Link, Typography } from "@mui/material";
import logo from "../../public/Logo.svg";

function Logo() {
  return (
    <Link
      underline="none"
      href="/"
      sx={{ display: "flex", alignItems: "center" }}
    >
      <img src={logo} alt="logo" />
      <Typography
        variant="h4"
        color="secondary"
        sx={{
          pl: "10px",
        }}
      >
        Interno
      </Typography>
    </Link>
  );
}

export default Logo;