import React from 'react'
import { Box, Button, Container, Grid, ThemeProvider, Typography, } from '@mui/material'
import whatwedo from '../../public/whatwedo.jpg'
import { appTheme } from '../../theme/theme'




const cards = [
    {
        id: 1,
        title: 'What We Do',
        text: 'It is a long established fact that a reader will be distracted by the of readable content of a page when lookings at its layouts the points of using   that it has a more-or-less normal',
        backgroundImage: "",
        button: 'Our Concept'

    },
    {
        id: 2,
        title: '',
        text: '',
        backgroundImage: `url(${whatwedo})`,
        button: ''
    },
    {
        id: 3,

        title: '',
        text: '',
        backgroundImage: `url(${whatwedo})`,
        button: ''
    },
    {
        id: 4,

        title: 'The End Result',
        text: 'It is a long established fact that a reader will be distracted by the of readable content of a page when lookings at its layouts the points of using   that it has a more-or-less normal',
        backgroundImage: `url()`,
        button: 'Our Portfolio'
    }


]


export default function WhatWeDo() {
    return (
        <ThemeProvider theme={appTheme}>
            <Box sx={{ padding: "200px 0" }}>
                <Container sx={{ width: "90%", margin: "0 auto" }}>
                    <Grid container spacing={{ sm: 4 }} justifyContent='center' >
                        {cards.map((elem, i) => {
                            return (
                                <Grid item xs={12} sm={6} md={6} key={i}  >
                                    <Box sx={{

                                        mb: 8,
                                        backgroundImage: elem.backgroundImage,
                                        height: "350px",
                                        backgroundPosition: 'center',
                                        backgroundRepeat: "no-repeat",
                                        borderRadius: '60px',
                                     }}>
                                        <Typography variant='h3' sx={{ pb: 3 }}>{elem.title}</Typography>
                                        <Typography variant='body1' sx={{ pb: 2, }}>{elem.text}</Typography>
                                        <Box >
                                            <Button
                                                sx={{
                                                    bgcolor: appTheme.palette.secondary.main,
                                                    textTransform: "capitalize",
                                                    padding: "8px 20px",
                                                    borderRadius: "15px",
                                                    color: "white"
                                                }}>{elem.button}
                                            </Button>
                                        </Box>
                                    </Box>
                                </Grid>
                            )
                        })}
                    </Grid>
                </Container>
            </Box>
        </ThemeProvider >
    )
}
