import { Box, Container, Divider, Grid, Typography } from "@mui/material";
import React from "react";
import { v4 as uuidv4 } from "uuid";

function Expirience() {
  const achievements = [
    { number: 12, desc: "Years Of Experiance" },
    { number: 85, desc: "success project" },
    { number: 15, desc: "Active project" },
    { number: 95, desc: "happy customers" },
  ];
  return (
    <Box
      component="section"
      sx={{
        bgcolor: "custom.main",
      }}
      mb={10}
    >
      <Container>
        <Grid
          container
          py={{ xs: 5, sm: 10, md: 15 }}
          justifyContent="space-between"
          spacing={3}
        >
          {achievements.map((ach, index) => (
            <>
              <Grid item key={uuidv4()} xs={12} sm={2.8} textAlign="center">
                <Typography variant="h1" color="primary.main">
                  {ach.number}
                </Typography>
                <Typography
                  variant="body1"
                  color="custom1.main"
                  sx={{ textTransform: "capitalize" }}
                >
                  {ach.desc}
                </Typography>
              </Grid>
              {index < achievements.length - 1 && (
                <Divider
                  orientation="vertical"
                  sx={{ background: "#CDA274", marginTop: "32px" }}
                  flexItem
                />
              )}
            </>
          ))}
        </Grid>
      </Container>
    </Box>
  );
}

export default Expirience;