import { AppBar, Container, Grid } from "@mui/material";
import Navbar from "./components/Navbar";
import Logo from './components/common/Logo';
import { Menu } from "@mui/icons-material";
import { useState } from "react";
import MenuDrawer from "./components/header/MenuDrawer"

function Header() {
  const [open, setOpen] = useState(false);
  function toggleClick() {
    setOpen(!open);
  }

  return (
    <AppBar
      position="sticky"
      sx={{ backgroundColor: "#fff", boxShadow: "0 0 0" }}
    >
      <Container>
        <Grid
          container
          maxWidth="1200px"
          sx={{
            display: "flex",
            justifyContent: "space-between",
            alignItems: "center",
            py: 3,
          }}
        >
          <Grid item sx={{ width: { md: 1 / 2, sm: 1 / 3, xs: 1 / 2 } }}>
            <Logo />
          </Grid>
          <Grid
            item
            sx={{
              width: { md: 1 / 2, sm: 3 / 5 },
              display: { sm: "flex", xs: "none" },
              justifyContent: "space-between",
            }}
          >
            <Navbar />
          </Grid>
          <Grid item sx={{ display: { sm: "none", xs: "flex" } }}>
            <Menu onClick={toggleClick} color="primary" fontSize="large" />
            <MenuDrawer
              open={open}
              onOpen={toggleClick}
              onClose={toggleClick}
            />
          </Grid>
        </Grid>
      </Container>
    </AppBar>
  );
}

export default Header;